import { RouteUrlList } from '@/libs/RouteList';

import type { SystemDTO } from '@/models/DTO/SystemDTO';

const mockSystemData: SystemDTO[] = Array.from({ length: 30 }, (_, i) => ({
    // business: `BIZ${i + 1}`,
    system: `SYS${i + 1}`,
    isActive: i % 3 !== 0 ? 'Y' : 'N', // 每三筆關閉一筆
    isDeleted: i % 10 === 0, // 每十筆標示刪除
    apiKey: `token${i + 1}`,
    hashKey: `hashKey${i + 1}`,
    iVKey: `iVKey${i + 1}`,
    //base64TokenSignature: btoa(`token${i + 1}_signature`),
    //base64Salt: btoa(`salt${i + 1}`),
    // pepper: `pepper${i + 1}`,
    createdTime: new Date(2024, 0, 1 + i).toISOString(),
    lastUpdate: i % 4 === 0 ? null : new Date(2024, 5, 1 + i).toISOString()
}));

/**
 * @todo  ViewData
 */
const GetAllSystemInfo = [
    'POST',
    RouteUrlList.API_GetAllSystemInfo.path,
    {},
    200,
    {
        status: true,
        title: '',
        message: 'OK',
        errorCode: '',
        result: {
            items: mockSystemData
        }
    }
];
export default GetAllSystemInfo;
