import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { BusinessDTO } from '@/models/DTO/BusinessDTO';

/**
 * @todo 初始化設定值
 */
export interface BusinessManagementViewDataResp extends GeneralResp {
    result: {
        items: BusinessDTO[];
        totalCount: number;
    };
}

/**
 * @todo Get ViewData
 */
const BusinessManagementViewData = (
    business: string,
    pageNumber: number,
    pageSize: number
): Promise<BusinessManagementViewDataResp> => {
    const url = RouteUrlList.API_BusinessManagementViewData.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(
        url,
        {
            business: business,
            pageNumber: pageNumber,
            pageSize: pageSize
        },
        config
    ).then((res: AxiosResponse<BusinessManagementViewDataResp>) => {
        return res.data;
    });
};

export default BusinessManagementViewData;
