<template>
    <component :is="layoutComponent" @toggleTheme="toggleTheme"></component>
</template>

<script setup lang="ts">
import { computed, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import DefaultLayout from '@/components/layouts/DefaultLayout.vue';
import SidebarLayout from '@/components/layouts/SidebarLayout.vue';

import { RouteUrlList } from '@/libs/RouteList';

import { useBeforeUnload } from '@/hooks/useBeforeUnload';
import { usePreventPopState } from './hooks/usePreventPopState';
import { useLeaveProtectionStore } from '@/stores/modules/leaveProtection';
import * as Common from '@/libs/Common';
import 'animate.css';

const route = useRoute();
useBeforeUnload((event: BeforeUnloadEvent) => {
    // 使用自定義 hook 來處理頁面卸載事件
    const leaveProtectionStore = useLeaveProtectionStore();
    if (leaveProtectionStore.hasUnsavedChanges) {
        event.preventDefault(); // 必須調用以阻止行為
    }
});
// 阻止返回
usePreventPopState();

const layoutComponent = computed(() => {
    const layout = route.meta.layout;
    switch (layout) {
        case 'admin':
            return SidebarLayout;
        case 'default':
        default:
            return DefaultLayout;
    }
});

const toggleTheme = () => {
    const body = document.body;
    const isLightMode = body.getAttribute('data-bs-theme') === 'light';

    // 切換主題屬性
    body.setAttribute('data-bs-theme', isLightMode ? 'dark' : 'light');

    // 批量替換樣式
    if (isLightMode) {
        // 轉換成dark
        document.querySelectorAll('[data-bs-theme="light"]').forEach((el) => {
            el.setAttribute('data-bs-theme', 'dark');
        });

        document.querySelectorAll('.bg-light').forEach((el) => {
            el.classList.remove('bg-light');
            el.classList.add('bg-dark');
        });

        document.querySelectorAll('.bg-white').forEach((el) => {
            el.classList.remove('bg-white');
            el.classList.add('bg-dark');
        });
        document.querySelectorAll('.text-dark').forEach((el) => {
            el.classList.remove('text-dark');
            el.classList.add('text-light');
        });

        document.querySelectorAll('.navbar-light').forEach((el) => {
            el.classList.remove('navbar-light');
            el.classList.add('navbar-dark');
        });

        document.querySelectorAll('.btn-esun-outline-dark').forEach((el) => {
            el.classList.remove('btn-esun-outline-dark');
            el.classList.add('btn-esun-outline-light');
        });
    } else {
        document.querySelectorAll('[data-bs-theme="dark"]').forEach((el) => {
            el.setAttribute('data-bs-theme', 'light');
        });
        document.querySelectorAll('.bg-dark').forEach((el) => {
            el.classList.remove('bg-dark');
            el.classList.add('bg-light');
        });

        document.querySelectorAll('.bg-dark').forEach((el) => {
            el.classList.remove('bg-dark');
            el.classList.add('bg-light');
        });

        document.querySelectorAll('.text-light').forEach((el) => {
            el.classList.remove('text-light');
            el.classList.add('text-dark');
        });

        document.querySelectorAll('.navbar-dark').forEach((el) => {
            el.classList.remove('navbar-dark');
            el.classList.add('navbar-light');
        });

        document.querySelectorAll('.btn-esun-outline-light').forEach((el) => {
            el.classList.remove('btn-esun-outline-light');
            el.classList.add('btn-esun-outline-dark');
        });
    }

    // 更新按鈕文字
    const themeToggleBtn = document.getElementById('toggleTheme');
    if (themeToggleBtn) {
        themeToggleBtn.textContent = isLightMode ? '切換淺色模式' : '切換深色模式';
    }
};

onMounted(async () => {
    // 移除遮罩
    document.onreadystatechange = () => {
        if (document.readyState == 'complete') {
            document.querySelector('.loading')?.classList.add('loading--remove');
        }
    };
});
</script>

<style>
[v-cloak] {
    display: none !important;
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.5s;
}
.fade-enter-from,
.fade-leave-to {
    opacity: 0;
}

.inputAlert {
    color: #c92e34;
}
</style>
