import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { FileInfos } from '@/models/DTO/FileInfos';
import type { FileUploadResult } from '@/models/DTO/FileUploadResult';

/**
 * 新增請求參數
 */
export interface FileInfosManagementCreateReq {
    business: string;
    caseNo: string;
    fileName: string;
    docType: number;
    fileSrc: string | null;
    isActive: boolean;
    filePermission: string;
    description?: string | null;
    isUseRetention: boolean;
    deleteTime?: string | null;
    uploadDate?: string | null;
    isForceFileUpload?: boolean;
}

/**
 * 新增檔案資訊回應
 */
export interface FileInfosManagementCreateResp extends GeneralResp {
    result?: FileUploadResult;
}

/**
 * 新增檔案資訊
 */
const FileInfosManagementCreate = (req: FileInfosManagementCreateReq): Promise<FileInfosManagementCreateResp> => {
    const url = RouteUrlList.API_FileInfosManagementCreate.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };

    // 轉換資料格式以符合後端 API 期望
    const requestData = {
        business: req.business,
        caseNo: req.caseNo,
        fileName: req.fileName,
        docType: req.docType, // 文件類別
        isActive: req.isActive,
        filePermission: parseInt(req.filePermission), // 轉換為 int 型別
        isForceFileUpload: req.isForceFileUpload || false,
        description: req.description,
        fileSrc: req.fileSrc, // 將 fileSrc 對應到 filePath
        isUseRetention: req.isUseRetention,
        deleteTime: req.deleteTime ? new Date(req.deleteTime).toISOString() : null,
        uploadDate: req.uploadDate ? new Date(req.uploadDate).toISOString() : null
    };

    return ApiClient.post(url, requestData, config).then((res: AxiosResponse<FileInfosManagementCreateResp>) => {
        return res.data;
    });
};

export default FileInfosManagementCreate;
