import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';

/**
 * @todo 刪除 FileCase 回應
 */
export interface FileCaseManagementDeleteResp extends GeneralResp {}

/**
 * @todo 刪除 FileCase
 */
const FileCaseManagementDelete = (caseNo: string): Promise<GeneralResp> => {
    const url = RouteUrlList.API_FileCaseManagementDelete.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    
    // 轉換資料格式以符合後端 API 期望
    const requestData = {
        caseNo: caseNo
    };
    
    return ApiClient.post(url, requestData, config).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default FileCaseManagementDelete;