import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { SystemDTO } from '@/models/DTO/SystemDTO';

/**
 * @todo 初始化設定值
 */
export interface SystemListManagementCreateResp extends GeneralResp {}

/**
 * @todo 建立系統
 */
const SystemListManagementCreate = (system: SystemDTO): Promise<GeneralResp> => {
    const url = RouteUrlList.API_SysManagementCreate.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(
        url,
        {
            systemName: system.system,
            apiKey: system.apiKey === '' ? null : system.apiKey,
            isActivate: system.isActive === 'Y'
        },
        config
    ).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default SystemListManagementCreate;
