import { RouteUrlList } from '@/libs/RouteList';

import type { FileLabelDTO } from '@/models/DTO/FileLabelDTO';

const mockFileLabelData: FileLabelDTO[] = Array.from({ length: 20 }, (_, i) => ({
    labelID: i + 1,
    labelName: `Label ${i + 1}`,
    description: i % 5 === 0 ? null : `Description for Label ${i + 1}`,
    createdTime: new Date(2024, 0, 1 + i).toISOString(),
    lastUpdate: i % 3 === 0 ? null : new Date(2024, 6, 1 + i).toISOString()
}));

/**
 * @todo  ViewData
 */
const FileLabelManagementViewData = [
    'POST',
    RouteUrlList.API_FileLabelManagementViewData.path,
    {},
    200,
    {
        status: true,
        title: '',
        message: 'OK',
        errorCode: '',
        result: {
            items: mockFileLabelData
        }
    }
];

export default FileLabelManagementViewData;
