import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { BusinessDTO } from '@/models/DTO/BusinessDTO';

export interface AccessibleBusinessList {
    allowBusiness: string;
    business: string;
}
/**
 * @todo 初始化設定值
 */
export interface GetAccessibleBusinessResp extends GeneralResp {
    result: {
        items: AccessibleBusinessList[];
    };
}

/**
 * @todo Get ViewData
 */
const AccessibleBusinessViewData = (business: string): Promise<GetAccessibleBusinessResp> => {
    const url = RouteUrlList.API_AccessibleBusinessViewData.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, { business: business }, config).then((res: AxiosResponse<GetAccessibleBusinessResp>) => {
        return res.data;
    });
};

export default AccessibleBusinessViewData;
