import { createApp } from 'vue';
import { piniaInstance } from './stores/global';

import App from './App.vue';
import router from './router/index';

import VueDOMPurifyHTML from 'vue-dompurify-html';

// main.ts
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap';
import 'bootstrap-icons/font/bootstrap-icons.css';
import '@/assets/css/esun-theme.css';
import {
    SimplePopUp,
    SimplePopUpBtnType,
    SimplePopUpModalType,
    SimplePopUpSize,
    type SimplePopupResolveResult
} from '@/libs/SimplePopUp';

const app = createApp(App);

const simplePopUp = new SimplePopUp();
// 錯誤 處理
app.config.errorHandler = (err, instance, info) => {
    console.error('[Vue Error]', err, info);
    // 可以上報、提示、紀錄
    simplePopUp.showAlert(SimplePopUpModalType.Warning, '錯誤', info + err?.toString(), [
        {
            id: 'confirm',
            type: SimplePopUpBtnType.callback,
            btnClass: 'btn-info',
            resolveParam: 'confirm',
            title: '確定'
        }
    ]);
};

window.addEventListener('unhandledrejection', (event) => {
    console.error('[Global Promise Error]', event.reason);
    simplePopUp.showAlert(SimplePopUpModalType.Warning, '錯誤', event.reason, [
        {
            id: 'confirm',
            type: SimplePopUpBtnType.callback,
            btnClass: 'btn-info',
            resolveParam: 'confirm',
            title: '確定'
        }
    ]);
    event.preventDefault();
});

app.use(piniaInstance);
app.use(router);
app.use(VueDOMPurifyHTML);
app.mount('#app');
