import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { SystemDTO } from '@/models/DTO/SystemDTO';

/**
 * @todo 初始化設定值
 */
export interface BusinessManagementDeleteResp extends GeneralResp {}

/**
 * @todo 刪除業務別
 */
const BusinessManagementDelete = (business: string): Promise<GeneralResp> => {
    const url = RouteUrlList.API_BusinessManagementDelete.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, { business: business }, config).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default BusinessManagementDelete;
