import type { GeneralResp } from '@/models/GeneralResp';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { AxiosResponse } from 'axios';

/**
 * 新增文件類別權限白名單
 */
const FileCasePermissionWhiteListAdd = (caseNo: string, business: string): Promise<GeneralResp> => {
    const url = RouteUrlList.API_FileCasePermissionWhiteListAdd.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, { caseNo, business }, config).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default FileCasePermissionWhiteListAdd;