import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { FileCase } from '@/models/DTO/FileCase';

/**
 * @todo 更新 FileCase 回應
 */
export interface FileCaseManagementUpdateResp extends GeneralResp {}

/**
 * @todo 更新 FileCase
 */
const FileCaseManagementUpdate = (fileCase: FileCase): Promise<GeneralResp> => {
    const url = RouteUrlList.API_FileCaseManagementUpdate.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    
    // 轉換資料格式以符合後端 API 期望
    const requestData = {
        caseNo: fileCase.caseNo,
        business: fileCase.business,
        uploadSystemID: fileCase.uploadSystemID,
        uploadEmployeeNo: fileCase.uploadEmployeeNo,
        permission: parseInt(fileCase.permission.toString()) // 轉換為 int 型別
    };
    
    return ApiClient.post(url, requestData, config).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default FileCaseManagementUpdate;