import { RouteUrlList } from '@/libs/RouteList';
import type { FileInfos } from '@/models/DTO/FileInfos';
import type { FilePermission } from '@/models/DTO/FilePermission';

const mockFileInfosData: FileInfos[] = Array.from({ length: 30 }, (_, i) => ({
    system: `system-${i % 3}`,
    fileNo: `file-${i + 1}`,
    caseNo: `case-${i + 1}`,
    fileName: `file_${i + 1}.pdf`,
    filePath: `/nas/files/file_${i + 1}.pdf`,
    filePermission: (i % 4).toString() as FilePermission,
    uploadDate: new Date(2025, 5, 1 + i).toISOString(),
    docType: i % 3 + 1,
    isActive: i % 3 !== 0,
    isForceFileUpload: i % 2 === 0,
    description: i % 5 === 0 ? `描述${i + 1}` : null,
    isUseRetention: i % 4 === 0,
    deleteTime: i % 4 === 0 ? new Date(2025, 0, 1 + i).toISOString() : null,
    uploadSystem: `system-${i % 3}`,
    uploadEmployeeNo: `EMP${String(i + 1).padStart(3, '0')}`,
    docName: `文件類型${i % 3 + 1}`,
    totalCount: 30,
    contentType: 'application/pdf',
    business: `business-${i % 3 + 1}`
}));

/**
 * @todo  ViewData
 */
const FileInfosManagementViewData = [
    'POST',
    RouteUrlList.API_FileInfosManagementViewData.path,
    {},
    200,
    (config: any) => {
        // 解析請求資料
        const requestData = config.data ? JSON.parse(config.data) : {};
        const pageNumber = requestData.pageNumber || 1;
        const pageSize = requestData.pageSize || 10;
        const searchQuery = requestData.searchQuery || '';

        // 根據搜尋條件過濾資料
        let filteredData = mockFileInfosData;
        if (searchQuery) {
            const query = searchQuery.toLowerCase();
            filteredData = mockFileInfosData.filter(
                (item) =>
                    item.fileName.toLowerCase().includes(query) ||
                    item.fileNo.toLowerCase().includes(query) ||
                    item.caseNo.toLowerCase().includes(query) ||
                    (item.description && item.description.toLowerCase().includes(query))
            );
        }

        // 分頁處理
        const startIndex = (pageNumber - 1) * pageSize;
        const endIndex = startIndex + pageSize;
        const paginatedItems = filteredData.slice(startIndex, endIndex);

        return {
            status: true,
            title: '',
            message: 'OK',
            errorCode: '',
            result: {
                items: paginatedItems,
                totalItems: filteredData.length
            }
        };
    }
];

export default FileInfosManagementViewData;
