import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';

/**
 * @todo 初始化設定值
 */
export interface DocTypeManagementDeleteResp extends GeneralResp {}

/**
 * @todo 刪除文件類型
 */
const DocTypeManagementDelete = (docType: number): Promise<GeneralResp> => {
    const url = RouteUrlList.API_DocTypeManagementDelete.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, { docType: docType }, config).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default DocTypeManagementDelete;