import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { FileCase } from '@/models/DTO/FileCase';

/**
 * @todo 分頁請求參數
 */
export interface FileCaseManagementViewDataReq {
    pageNumber?: number;
    pageSize?: number;
    searchQuery?: string;
}

/**
 * @todo 初始化設定值
 */
export interface FileCaseManagementViewDataResp extends GeneralResp {
    result: {
        items: FileCase[];
        totalItems?: number;
    };
}

/**
 * @todo Get ViewData
 */
const FileCaseManagementViewData = (req?: FileCaseManagementViewDataReq): Promise<FileCaseManagementViewDataResp> => {
    const url = RouteUrlList.API_FileCaseManagementViewData.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };

    const requestData = {
        pageNumber: req?.pageNumber || 1,
        pageSize: req?.pageSize || 10,
        searchQuery: req?.searchQuery || ''
    };

    return ApiClient.post(url, requestData, config).then((res: AxiosResponse<FileCaseManagementViewDataResp>) => {
        return res.data;
    });
};

export default FileCaseManagementViewData;
