import './SimplePopUp.css';
import * as Common from '@/libs/Common';
import DOMPurify from 'dompurify';

export enum SimplePopUpModalType {
    Info = 'Info',
    Warning = 'Warning',
    Success = 'Success',
    Html = 'Html'
}

export enum SimplePopUpSize {
    sm = 'sm',
    lg = 'lg',
    xl = 'xl',
    full = 'full'
}

export enum SimplePopUpBtnType {
    href = 'href',
    href_blank = 'href_blank',
    callback = 'callback'
}

export interface ISimplePopUpBtnOption<T> {
    id: string;
    type: SimplePopUpBtnType;
    btnClass?: string | 'btn-primary';
    btnDisabled?: boolean;
    resolveParam?: T;
    title: string;
    href?: string;
}

export interface SimplePopupResolveResult<T = any> {
    type: 'callback' | 'href' | 'href_blank' | 'hide' | 'timeout';
    param: T | null;
    event: Event | null;
}
interface ISimplePopUp {
    timerInterval: number;
    showAlert<T>(
        type: SimplePopUpModalType,
        title: string | undefined | null,
        msg: string | undefined | null,
        buttonOptions?: Array<ISimplePopUpBtnOption<T>>,
        modalSize?: SimplePopUpSize,
        preventClose?: boolean,
        didOpen?: (arg?: any) => any,
        timerSec?: number
    ): Promise<SimplePopupResolveResult<T>>;

    show<T>(
        html: string | undefined | null,
        buttonOptions?: Array<ISimplePopUpBtnOption<any>>,
        modalSize?: SimplePopUpSize,
        preventClose?: boolean,
        didOpen?: (arg?: any) => any,
        timerSec?: number
    ): Promise<SimplePopupResolveResult<T>>;
    hide(): void;
    thread(): Array<any>;
}

export class SimplePopUp implements ISimplePopUp {
    constructor() {
        this.timerInterval = 0;
    }
    timerInterval = 0;
    thread<T = any>(): [Promise<T>, (value: T) => void, (reason?: any) => void] {
        let resolve: (value: T) => void;
        let reject: (reason?: any) => void;
        const promise = new Promise<T>((res, rej) => {
            resolve = res;
            reject = rej;
        });
        return [promise, resolve!, reject!];
    }

    showAlert<T>(
        type: SimplePopUpModalType,
        title: string | undefined | null = '',
        msg: string | undefined | null = '',
        buttonOptions?: Array<ISimplePopUpBtnOption<T>>,
        modalSize: SimplePopUpSize = SimplePopUpSize.lg,
        preventClose: boolean = false,
        didOpen?: (arg?: any) => void,
        timerSec?: number,
        preventTimeoutClose: boolean = false
    ): Promise<any> {
        let icon = '';
        let text_color = 'text-primary';
        if (type === SimplePopUpModalType.Warning) {
            icon = `<img class="icon " src='data:image/svg+xml;base64,'/>`;
            text_color = 'text-orange';
        } else if (type === SimplePopUpModalType.Success) {
            icon = '';
        }

        const templete = `<h3 class="p1 py-3 ${text_color} text-center">${icon}${title}</h3>
        <p class="p2 mb-4 text-center" style="white-space: pre-line;">${msg}</p>`;
        return this.show(templete, buttonOptions, modalSize, preventClose, didOpen, timerSec, preventTimeoutClose);
    }

    show<T>(
        html: string | undefined | null = '',
        buttonOptions?: Array<ISimplePopUpBtnOption<T>>,
        modalSize: SimplePopUpSize = SimplePopUpSize.lg,
        preventClose: boolean = false,
        didOpen?: (arg?: any) => void,
        timerSec?: number,
        preventTimeoutClose: boolean = false
    ) {
        const popUpHtml: HTMLElement | null = document.getElementById('SimplePopupHtmlOuter');
        if (popUpHtml) {
            popUpHtml.remove();
        }
        const [prompt, resolve] = this.thread();

        let buttonHtml = '';
        if (typeof buttonOptions !== 'undefined') {
            buttonOptions.forEach((element) => {
                if (element.type === 'callback') {
                    if (element.btnDisabled) {
                        buttonHtml += `<button disabled id="${element.id}" class="SimplePopupBtn  ${element.btnClass}">${element.title}</button>`;
                    } else {
                        buttonHtml += `<button id="${element.id}" class="SimplePopupBtn  ${element.btnClass}">${element.title}</button>`;
                    }
                } else if (element.type === 'href') {
                    buttonHtml += `<a id="${element.id}" class="SimplePopupBtn  ${element.btnClass}" href="${element.href}">${element.title}</a>`;
                } else if (element.type === 'href_blank') {
                    buttonHtml += `<a id="${element.id}" class="SimplePopupBtn   ${element.btnClass}" href="${element.href}" target="_blank">${element.title}</a>`;
                }
            });
        }

        const hideEvent = this.hide;
        const _newPopupHtmlOuter = `<div id="SimplePopupHtmlOuter" class="overlay-bg" style="display: block;">
            <div id="SimplePopupHtmlOverlay"  class="overlay"></div>
            <div class="SimplePopup ${modalSize}" id="popup1">
                <a id="closeBtn" class="cross ">
                <i class="bi bi-x-lg"></i>
                </a>
                <div class="mt-3">
                ${html}
                </div>
                
                <div id="SimplePopupBtnOuter" class="d-flex justify-content-center pt-2">
                    ${buttonHtml}
                </div>
        </div>`;
        const newPopupHtmlOuter = DOMPurify.sanitize(_newPopupHtmlOuter);
        document.body.insertAdjacentHTML('beforeend', newPopupHtmlOuter);

        if (timerSec) {
            clearInterval(this.timerInterval);
            setTimeout(() => {
                const b = document.querySelector('.timer') || null;
                if (b) {
                    let timer = timerSec !== undefined ? timerSec * 1000 : 30 * 1000;

                    // b.textContent = b.textContent = Common.GetLeftTimeText(timer);
                    this.timerInterval = window.setInterval(() => {
                        if (timer <= 0) {
                            b.textContent = Common.GetLeftTimeText(timer);
                            if (this.timerInterval) {
                                clearInterval(this.timerInterval);
                                if (preventTimeoutClose) {
                                    resolve({
                                        type: 'timeout',
                                        param: null,
                                        event: null
                                    } as SimplePopupResolveResult<T>);
                                } else {
                                    hideEvent();
                                    resolve({
                                        type: 'hide',
                                        param: null,
                                        event: null
                                    } as SimplePopupResolveResult<T>);
                                }
                            }
                        } else {
                            b.textContent = Common.GetLeftTimeText(timer);
                            timer -= 1000;
                        }
                    }, 1000);
                }
            });
        }

        /**
         * did open
         */
        if (typeof buttonOptions !== 'undefined') {
            buttonOptions.forEach((element) => {
                if (element.type === 'callback' || element.type === 'href' || element.type === 'href_blank') {
                    const _btnElement: HTMLElement | null = document.getElementById(element.id);
                    if (_btnElement) {
                        _btnElement.addEventListener('click', (event) => {
                            hideEvent();
                            resolve({
                                type: element.type,
                                param: element.resolveParam,
                                event: event
                            } as SimplePopupResolveResult<T>);
                        });
                    }
                }
            });
        }
        if (typeof didOpen !== 'undefined') {
            didOpen();
        }
        /**
         * will close
         */

        const SimplePopupHtmlOverlay: HTMLElement | null = document.getElementById('SimplePopupHtmlOverlay');
        if (SimplePopupHtmlOverlay && preventClose === false) {
            SimplePopupHtmlOverlay.addEventListener('click', (event) => {
                hideEvent();
                resolve({
                    type: 'hide',
                    param: null,
                    event: event
                } as SimplePopupResolveResult<T>);
            });
        }
        document.body.style.overflow = 'hidden';
        const closeBtn: HTMLElement | null = document.getElementById('closeBtn');
        if (closeBtn) {
            closeBtn.addEventListener('click', (event) => {
                hideEvent();
                resolve({
                    type: 'hide',
                    param: null,
                    event: event
                } as SimplePopupResolveResult<T>);
            });
        }
        return prompt;
    }

    /**
     * will close
     */
    hide() {
        document.body.style.overflow = 'auto';
        const popUpHtml: HTMLElement | null = document.getElementById('SimplePopupHtmlOuter');
        if (popUpHtml) {
            let opacity = 1;
            const interval = setInterval(function () {
                if (opacity > 0) {
                    opacity -= 0.1;
                    popUpHtml.style.opacity = opacity.toString();
                } else {
                    clearInterval(interval);
                    popUpHtml.style.display = 'none';
                    popUpHtml.remove();
                }
            }, 10);
        }
    }
}
