import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';

export interface GetFileContentReq {
    business: string;
    fileNo: string;
}

/**
 * 獲取檔案內容 - 返回 Blob 數據
 */
const GetFileContent = (request: GetFileContentReq): Promise<Blob> => {
    const url = RouteUrlList.API_FileInfosFileContent.path;
    const config = {
        params: { ...request },
        responseType: 'blob' as const
    };

    return ApiClient.get(url, config).then((res: AxiosResponse<Blob>) => {
        return res.data;
    });
};

export default GetFileContent;
