import { RouteUrlList } from '@/libs/RouteList';

import type { BusinessDTO } from '@/models/DTO/BusinessDTO';

const mockBusinessData: BusinessDTO[] = Array.from({ length: 30 }, (_, i) => ({
    business: `BIZ${i + 1}`,
    system: `SYS${(i % 5) + 1}`,
    isActive: i % 3 !== 0 ? 'Y' : 'N', // 每三筆關閉一筆
    isDeleted: i % 10 === 0 ? 'Y' : 'N', // 每十筆標示刪除
    token: `token${i + 1}`,
    base64TokenSignature: btoa(`token${i + 1}_signature`),
    base64Salt: btoa(`salt${i + 1}`),
    pepper: `pepper${i + 1}`,
    createdTime: new Date(2024, 0, 1 + i).toISOString(),
    lastUpdate: i % 4 === 0 ? null : new Date(2024, 5, 1 + i).toISOString()
}));

/**
 * @todo  ViewData
 */
const BusinessManagementViewData = [
    'POST',
    RouteUrlList.API_BusinessManagementViewData.path,
    {},
    200,
    {
        status: true,
        title: '',
        message: 'OK',
        errorCode: '',
        result: {
            items: mockBusinessData
        }
    }
];

export default BusinessManagementViewData;
