/**
 * @todo 取得 route info by url path
 * @param path
 * @returns
 */
const GetRouteDataByUrl = (path: string): IRouteUrlListElement => {
    let targetRouteData = RouteUrlList.StateError;
    for (const key in RouteUrlList) {
        const url: IRouteUrlListElement | null = RouteUrlList[key] || null;

        if (!url) {
            continue;
        }

        const hasMatch: boolean = url.path.toLowerCase().indexOf(path.trim().toLowerCase()) !== -1;
        if (hasMatch) {
            targetRouteData = url;
            break;
        }
    }
    return targetRouteData;
};
export { RouteUrlList, GetRouteDataByUrl };
export type { IRouteUrlListElement, IRouteUrlList };
