<script setup lang="ts">
import { RouteUrlList } from '@/libs/RouteList';
import { ref, defineProps, onMounted, watch } from 'vue';
import { useRoute, type RouteLocationNormalizedLoaded } from 'vue-router';

const props = defineProps<{ collapsed: boolean }>();
const active = ref<string[] | null>(null);
const route: RouteLocationNormalizedLoaded = useRoute();

function toggle(name: string) {
    if (!active.value) {
        active.value = [name];
    } else if (active.value.includes(name)) {
        const newArr = active.value.filter((item) => item !== name);
        active.value = newArr.length ? newArr : null;
    } else {
        active.value = [...active.value, name];
    }
}

function setActiveByRoute() {
    const path = route.path;
    if (
        path.startsWith(RouteUrlList.SysManagement.path) ||
        path.startsWith(RouteUrlList.SysManagement.path) ||
        path.startsWith(RouteUrlList.BusinessManagement.path)
    ) {
        active.value = ['system'];
    } else if (
        path.startsWith(RouteUrlList.DocTypeManagement.path) ||
        path.startsWith(RouteUrlList.FileCaseManagement.path) ||
        path.startsWith(RouteUrlList.FileInfosManagement.path) ||
        path.startsWith(RouteUrlList.FileLabelManagement.path)
    ) {
        active.value = ['file'];
    } else {
        active.value = ['system'];
    }
}

onMounted(setActiveByRoute);
watch(() => route.path, setActiveByRoute);
</script>
<template>
    <div class="sidebar sidebar-esun text-dark" :class="{ collapsed: collapsed }">
        <a href="#" @click.prevent="toggle('system')"
            ><i class="bi bi-files"></i>系統設定管理
            <i class="expand" :class="['bi', active?.includes('system') ? 'bi-chevron-up' : 'bi-chevron-down']"></i>
        </a>
        <div class="submenu" :class="{ active: active?.includes('system') }">
            <RouterLink
                v-bind:to="RouteUrlList.SysManagement.path"
                title="介接系統管理"
                :class="{
                    'active-link':
                        route.path === RouteUrlList.Index.path || route.path === RouteUrlList.SysManagement.path
                }"
                ><i class="bi bi-list"></i>介接系統管理
            </RouterLink>
            <RouterLink
                v-bind:to="RouteUrlList.BusinessManagement.path"
                title="介接業務別管理"
                :class="{ 'active-link': route.path === RouteUrlList.BusinessManagement.path }"
                ><i class="bi bi-list"></i>業務別管理</RouterLink
            >
        </div>

        <a href="#" @click.prevent="toggle('file')">
            <i class="bi bi-files"></i>檔案管理設定
            <i class="expand" :class="['bi', active?.includes('file') ? 'bi-chevron-up' : 'bi-chevron-down']"></i>
        </a>
        <div class="submenu" :class="{ active: active?.includes('file') }">
            <RouterLink
                v-bind:to="RouteUrlList.DocTypeManagement.path"
                title="文件類別管理"
                :class="{ 'active-link': route.path === RouteUrlList.DocTypeManagement.path }"
                ><i class="bi bi-list"></i>文件類別管理</RouterLink
            >

            <RouterLink
                v-bind:to="RouteUrlList.FileCaseManagement.path"
                title=" 檔案案件 管理"
                :class="{ 'active-link': route.path === RouteUrlList.FileCaseManagement.path }"
                ><i class="bi bi-list"></i>檔案案件管理</RouterLink
            >

            <RouterLink
                v-bind:to="RouteUrlList.FileInfosManagement.path"
                title=" 檔案案件 管理"
                :class="{ 'active-link': route.path === RouteUrlList.FileInfosManagement.path }"
                ><i class="bi bi-list"></i>檔案管理</RouterLink
            >

            <!--
            <RouterLink
                v-bind:to="RouteUrlList.FileLabelManagement.path"
                title=" 檔案案件 管理"
                :class="{ 'active-link': route.path === RouteUrlList.FileLabelManagement.path }"
                ><i class="bi bi-list"></i>檔案標籤管理</RouterLink
            > -->
        </div>
    </div>
</template>

<style scoped>
.sidebar {
    width: 250px;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background-color: #343a40;
    padding-top: 60px;
    transition: all 0.3s;
    overflow-x: hidden;
    overflow-y: auto;
    box-shadow: 2px 0 8px rgba(0, 0, 0, 0.3);
    border-right: 1px solid rgba(255, 255, 255, 0.1);
}
.sidebar.collapsed {
    width: 0;
    padding: 0;
    overflow: hidden;
}
.sidebar a {
    padding: 15px;
    text-decoration: none;
    font-size: 1.1rem;
    color: inherit;
    display: block;
    transition: color 0.3s ease;
}
.sidebar a > i {
    padding-right: 10px;
}
.sidebar a:hover {
    background-color: var(--esun-bg-accent);
    color: var(--esun-primary);
}
.submenu {
    display: none;
    padding-left: 20px;
}
.submenu a {
    font-size: 0.95rem;
}
.submenu.active {
    display: block;
}
/* Add this style to highlight the active link */
.active-link {
    background: linear-gradient(135deg, var(--esun-primary) 0%, var(--esun-primary-hover) 100%);
    color: white !important;
    transform: translateX(4px);
}

/* Dark mode font color fix */
[data-bs-theme='dark'] .sidebar {
    background-color: #212529;
}
[data-bs-theme='dark'] .sidebar a {
    color: #cfd8dc;
}
[data-bs-theme='dark'] .sidebar a:hover {
    background-color: #343a40;
    color: #ffffff;
}
[data-bs-theme='dark'] .active-link {
    background-color: #343a40;
    color: #ffffff;
}
</style>
