import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { SystemDTO } from '@/models/DTO/SystemDTO';

/**
 * @todo 初始化設定值
 */
export interface SystemDeactivate extends GeneralResp {}

/**
 * @todo 停用系統
 */
const SystemDeactivate = (systemId: string): Promise<GeneralResp> => {
    const url = RouteUrlList.API_SystemDeactivate.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, { systemId: systemId }, config).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default SystemDeactivate;
