import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { DocTypeDTO } from '@/models/DTO/DocType';

/**
 * @todo 初始化設定值
 */
export interface DocTypeManagementViewDataResp extends GeneralResp {
    result: {
        items: DocTypeDTO[];
        totalCount: number;
    };
}

/**
 * @todo Get ViewData
 */
const DocTypeManagementViewData = (
    pageNumber: number,
    pageSize: number,
    docName: string
): Promise<DocTypeManagementViewDataResp> => {
    const url = RouteUrlList.API_DocTypeManagementViewData.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, { pageNumber: pageNumber, pageSize: pageSize, docName: docName }, config).then(
        (res: AxiosResponse<DocTypeManagementViewDataResp>) => {
            return res.data;
        }
    );
};

export default DocTypeManagementViewData;
