import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { SystemDTO } from '@/models/DTO/SystemDTO';

/**
 * @todo 初始化設定值
 */
export interface SystemListManagementDeleteResp extends GeneralResp {}

/**
 * @todo 登入檢查
 */
const SystemListManagementDelete = (system: string): Promise<GeneralResp> => {
    const url = RouteUrlList.API_SysManagementDelete.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, { deleteSystem: system }, config).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default SystemListManagementDelete;
