import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { DocTypeDTO } from '@/models/DTO/DocType';

/**
 * @todo 初始化設定值
 */
export interface DocTypeManagementCreateResp extends GeneralResp {}

/**
 * @todo 建立文件類型
 */
const DocTypeManagementCreate = (docType: DocTypeDTO): Promise<GeneralResp> => {
    const url = RouteUrlList.API_DocTypeManagementCreate.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(
        url,
        {
            docName: docType.docName,
            description: docType.description,
            permission: docType.permission
        },
        config
    ).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default DocTypeManagementCreate;
