import { RouteUrlList } from '@/libs/RouteList';
import type { FileCase } from '@/models/DTO/FileCase';
import { FileCasePermission } from '@/models/DTO/FileCasePermission';

const mockFileCaseData: FileCase[] = Array.from({ length: 30 }, (_, i) => ({
    caseNo: `CASE${i + 1}`,
    business: `BIZ${(i % 5) + 1}`,
    uploadSystemID: i % 2 === 0 ? `SYS${(i % 3) + 1}` : null,
    uploadEmployeeNo: i % 2 !== 0 ? `EMP${1000 + i}` : null,
    permission: [
        0, // FileCasePermission.Public
        1, // FileCasePermission.Protect
        2, // FileCasePermission.ProtectInternal
        3 // FileCasePermission.Private
    ][i % 4],
    updateTime: new Date(2024, 0, 1 + (i % 30), 10, 30, 0).toISOString()
}));

/**
 * @todo  ViewData
 */
const FileCaseManagementViewData = [
    'POST',
    RouteUrlList.API_FileCaseManagementViewData.path,
    {},
    200,
    (config: any) => {
        // 解析請求資料
        const requestData = config.data ? JSON.parse(config.data) : {};
        const pageNumber = requestData.pageNumber || 1;
        const pageSize = requestData.pageSize || 10;
        const searchQuery = requestData.searchQuery || '';

        // 根據搜尋條件過濾資料
        let filteredData = mockFileCaseData;
        if (searchQuery) {
            const query = searchQuery.toLowerCase();
            filteredData = mockFileCaseData.filter(
                (item) =>
                    item.caseNo.toLowerCase().includes(query) ||
                    item.business.toLowerCase().includes(query) ||
                    (item.uploadSystemID && item.uploadSystemID.toLowerCase().includes(query)) ||
                    (item.uploadEmployeeNo && item.uploadEmployeeNo.toLowerCase().includes(query))
            );
        }

        // 分頁處理
        const startIndex = (pageNumber - 1) * pageSize;
        const endIndex = startIndex + pageSize;
        const paginatedItems = filteredData.slice(startIndex, endIndex);

        return {
            status: true,
            title: '',
            message: 'OK',
            errorCode: '',
            result: {
                items: paginatedItems,
                totalItems: filteredData.length
            }
        };
    }
];

export default FileCaseManagementViewData;
