<template>
    <div class="esun-theme bg-light" data-bs-theme="light">
        <TopNavbar @toggle-sidebar="isCollapsed = !isCollapsed" @toggleTheme="emit_toggleTheme" />
        <Sidebar :collapsed="isCollapsed" :class="{ expanded: !isCollapsed }" />
        <!-- render body start-->
        <RouterView v-slot="{ Component }">
            <transition
                enter-active-class="animate__animated animate__fadeIn"
                leave-active-class="animate__animated animate__fadeOut"
            >
                <div class="container-fluid router-view-container">
                    <div
                        :key="$route.fullPath"
                        class="content"
                        :class="{ expanded: isCollapsed }"
                        ref="scrollContainerRef"
                    >
                        <component :is="Component" :collapsed="isCollapsed" @toggleTheme="emit_toggleTheme" />
                    </div>
                </div>
            </transition>
        </RouterView>
        <!-- render body end -->
        <GoTop />
    </div>
</template>

<script setup lang="ts">
import { ref } from 'vue';
import isMobile from 'ismobilejs';

import { RouterView } from 'vue-router';
import TopNavbar from '@/components/share/TopNavbar.vue';
import Sidebar from '@/components/share/Sidebar.vue';
import GoTop from '@/components/share/GoTop.vue';

const scrollContainerRef = ref<HTMLElement | null>(null);

const _isMobile: boolean = isMobile(window.navigator).any;
const isCollapsed = ref(false);
if (_isMobile) {
    isCollapsed.value = true;
}
const emit = defineEmits(['toggleTheme']);
function emit_toggleTheme() {
    emit('toggleTheme');
}
</script>

<style>
/* 桌面裝置原有樣式 */
.content {
    margin-left: 250px;
    transition: margin-left 0.3s;
    width: calc(100% - 250px);
    overflow-y: auto;
    min-height: 100vh;
}
.content.expanded {
    margin-left: 0;
    width: 100%;
}

/* 行動裝置寬度小於 480px 時，sidebar 寬度設為 100vw，且展開時 .content 隱藏 */
@media (max-width: 480px) {
    .sidebar {
        width: 0;
        transition: width 0.3s;
        z-index: 20; /* 設定 sidebar z-index 大於 content */
    }
    .sidebar.expanded {
        width: 100vw !important;
        z-index: 20; /* 確保展開時 z-index 仍然較高 */
    }
    .content.expanded {
        display: block !important;
    }
    .content {
        display: none !important;
    }
}
</style>
