import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { FileLabelDTO } from '@/models/DTO/FileLabelDTO';

/**
 * @todo 初始化設定值
 */
export interface FileLabelManagementViewDataResp extends GeneralResp {
    data: {
        items: FileLabelDTO[];
    };
}

/**
 * @todo Get ViewData
 */
const FileLabelManagementViewData = (): Promise<FileLabelManagementViewDataResp> => {
    const url = RouteUrlList.API_FileLabelManagementViewData.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, {}, config).then((res: AxiosResponse<FileLabelManagementViewDataResp>) => {
        return res.data;
    });
};

export default FileLabelManagementViewData;
