import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { BusinessDTO } from '@/models/DTO/BusinessDTO';

export interface BusinessManagementUpdateParams {
    business: string;
    systemName: string;
    isDeleted: boolean;
    isActive: boolean;
    token: string;
}

/**
 * @todo 初始化設定值
 */
export interface BusinessManagementUpdateResp extends GeneralResp {}

/**
 * @todo 更新業務別
 */
const BusinessManagementUpdate = (business: BusinessManagementUpdateParams): Promise<GeneralResp> => {
    const url = RouteUrlList.API_BusinessManagementUpdate.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, business, config).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default BusinessManagementUpdate;
