import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { SystemDTO } from '@/models/DTO/SystemDTO';

/**
 * @todo 初始化設定值
 */
export interface GetAllSystemInfoResp extends GeneralResp {
    result: {
        items: SystemDTO[];
    };
}

/**
 * @todo 取得所有系統資訊
 */
const GetAllSystemInfo = (): Promise<GetAllSystemInfoResp> => {
    const url = RouteUrlList.API_GetAllSystemInfo.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, {}, config).then((res: AxiosResponse<GetAllSystemInfoResp>) => {
        return res.data;
    });
};

export default GetAllSystemInfo;
