import { RouteUrlList } from '@/libs/RouteList';
import type { DocTypeDTO } from '@/models/DTO/DocType';

const mockDocTypeData: DocTypeDTO[] = Array.from({ length: 30 }, (_, i) => ({
    docType: 1000 + i,
    docName: `文件類型${i + 1}`,
    description: i % 5 === 0 ? null : `描述${i + 1}`,
    permission: (i % 4).toString(),
    isDeleted: i % 10 === 0,
    createdTime: new Date(2024, 0, 1 + i).toISOString(),
    lastUpdate: i % 4 === 0 ? null : new Date(2024, 5, 1 + i).toISOString()
}));

/**
 * @todo  ViewData
 */
const DocTypeManagementViewData = [
    'POST',
    RouteUrlList.API_DocTypeManagementViewData.path,
    {},
    200,
    {
        status: true,
        title: '',
        message: 'OK',
        errorCode: '',
        result: {
            items: mockDocTypeData
        }
    }
];

export default DocTypeManagementViewData;
