import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { DocTypeDTO } from '@/models/DTO/DocType';

/**
 * @todo 初始化設定值
 */
export interface DocTypeManagementUpdateResp extends GeneralResp {}

/**
 * @todo 更新文件類型
 */
const DocTypeManagementUpdate = (item: DocTypeDTO): Promise<GeneralResp> => {
    const url = RouteUrlList.API_DocTypeManagementUpdate.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(
        url,
        {
            docType: item.docType,
            docName: item.docName,
            description: item.description,
            permission: item.permission,
            isDeleted: item.isDeleted
        },
        config
    ).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default DocTypeManagementUpdate;
