interface IOptions {
    [key: string]: string | null | number | boolean;
    containerID: string | null;
    backgroundColor: string;
    backgroundOpacity: number;
    disableScroll: boolean;

    overlayID: string;
    overlayZIndex: number;

    spinnerID: string;
    spinnerSize: number;
    spinnerZIndex: number;
}

interface ILoadingOverLay {
    options: IOptions;
    spinnerHtml: string;
    show(options: IOptions | undefined | null, spinnerHtml: string | undefined | null): void;
    hide(): void;
}

export class LoadingOverLay implements ILoadingOverLay {
    options: IOptions;
    spinnerHtml: string;

    constructor() {
        this.options = {
            containerID: null,
            backgroundColor: '#eee',
            backgroundOpacity: 0.6,
            disableScroll: false,
            overlayID: 'MyOverLay',
            overlayZIndex: 99998,

            spinnerID: 'MySpinner',
            spinnerSize: 50,
            spinnerZIndex: 99999
        };
        this.spinnerHtml = `<img style="width: 80px;" src="data:image/gif;base64,"/><span>讀取中</span>`;
    }

    /**
     * 顯示遮罩
     */
    show(options: IOptions | undefined | null, spinnerHtml: string | undefined | null): void {
        this.SetOptions(options);
        this.hide();
        //
        this.SetSpinner(spinnerHtml);
        if (this.options.disableScroll) {
            document.body.style.overflow = 'hidden';
            document.documentElement.style.overflow = 'hidden';
        }
        // 產生遮罩
        this.CreateOverlayElement();
    }

    /**
     * 隱藏遮罩
     */
    hide() {
        // remove disabled scroll style
        if (this.options.disableScroll) {
            document.body.style.overflow = '';
            document.documentElement.style.overflow = '';
        }

        document.getElementById(this.options.overlayID)?.remove();
        document.getElementById(this.options.spinnerID)?.remove();
    }

    SetOptions(options: IOptions | undefined | null): void {
        if (typeof options !== 'undefined') {
            for (const key in options) {
                this.options[key] = options[key];
            }
        }
    }

    SetSpinner(html: string | undefined | null): void {
        if (typeof html !== 'undefined' && html !== null && html.trim()) {
            this.spinnerHtml = html;
        }
    }

    CreateOverlayElement(): void {
        const left: string = '50%';
        // left = `calc(50% + ${this.options.offsetX})`
        const top: string = '50%';
        //  top = `calc(50% + ${this.options.offsetY})`
        // Generate overlay html element in container.
        if (
            this.options.containerID &&
            document.body.contains(document.getElementById(this.options.containerID))
        ) {
            const loadingOverlay: string = `<div id="${this.options.overlayID}" 
                style="display: block !important; position: absolute; top: 0; left: 0; overflow: auto; cursor: wait;
                opacity: ${this.options.backgroundOpacity}; 
                background: ${this.options.backgroundColor}; 
                z-index: 50; width: 100%; height: 100%;">
            </div>
            <div id="${this.options.spinnerID}" 
                style="
                display: flex;
                -webkit-box-pack: center;
                -ms-flex-pack: center;
                justify-content: center;
                -webkit-box-align: center;
                -ms-flex-align: center;
                align-items: center;
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                -ms-flex-direction: column;
                flex-direction: column;
                position: absolute; 
                -webkit-transform: translate(-50%); 
                -ms-transform: translate(-50%); 
                transform: translate(-50%); 
                z-index: 9999;">${this.spinnerHtml}</div>`;

            const containerElement: HTMLElement | null = document.getElementById(
                this.options.containerID
            );
            if (containerElement) {
                containerElement.style.position = 'relative';
                containerElement.insertAdjacentHTML('beforeend', loadingOverlay);
            }
            return;
        }

        const loadingOverlay = `<div id="${this.options.overlayID}" 
            style="display: block !important; position: fixed; top: 0; left: 0; overflow: auto; 
            background: ${this.options.backgroundColor}; 
            opacity: ${this.options.backgroundOpacity}; 
            z-index: ${this.options.overlayZIndex}; width: 100%; height: 100%;"></div>
            <div id="${this.options.spinnerID}" 
                style="
                display: flex;
                width:100%;
                height:100%;
                -webkit-box-pack: center;
                -ms-flex-pack: center;
                justify-content: center;
                -webkit-box-align: center;
                -ms-flex-align: center;
                align-items: center;
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                -ms-flex-direction: column;
                flex-direction: column;
                position: fixed; 
                top: 0; 
                left: 0; 
                z-index: ${this.options.spinnerZIndex};">${this.spinnerHtml}</div>`;

        document.body.insertAdjacentHTML('beforeend', loadingOverlay);
    }
}
