import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { FileInfos } from '@/models/DTO/FileInfos';

/**
 * 分頁請求參數
 */
export interface FileInfosManagementViewDataReq {
    pageNumber?: number;
    pageSize?: number;
    searchQuery?: string;
}

/**
 * 分頁回應格式
 */
export interface FileInfosManagementViewDataResp extends GeneralResp {
    result: {
        items: FileInfos[];
        totalCount: number;
    };
}

/**
 * Get ViewData with pagination
 */
const FileInfosManagementViewData = (
    req?: FileInfosManagementViewDataReq
): Promise<FileInfosManagementViewDataResp> => {
    const url = RouteUrlList.API_FileInfosManagementViewData.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };

    const requestData = {
        pageNumber: req?.pageNumber || 1,
        pageSize: req?.pageSize || 10,
        searchQuery: req?.searchQuery || ''
    };

    return ApiClient.post(url, requestData, config).then((res: AxiosResponse<FileInfosManagementViewDataResp>) => {
        return res.data;
    });
};

export default FileInfosManagementViewData;
