import { RouteUrlList } from '@/libs/RouteList';
import type { FileCasePermissionWhiteListItem } from '@/api/v1/5_FileCase/FileCasePermissionWhiteListViewData';

const mockWhiteListData: FileCasePermissionWhiteListItem[] = [
    { caseNo: 'CASE1', business: 'BIZ1' },
    { caseNo: 'CASE1', business: 'BIZ2' },
    { caseNo: 'CASE2', business: 'BIZ3' },
    { caseNo: 'CASE3', business: 'BIZ1' },
    { caseNo: 'CASE3', business: 'BIZ4' }
];

/**
 * 取得文件類別權限白名單資料
 */
const FileCasePermissionWhiteListViewData = [
    'GET',
    RouteUrlList.API_FileCasePermissionWhiteListViewData.path,
    {},
    200,
    {
        status: true,
        title: '',
        message: 'OK',
        errorCode: '',
        result: {
            items: mockWhiteListData.filter((item) => item.caseNo === 'CASE1') // 根據參數篩選
        }
    }
];

export default FileCasePermissionWhiteListViewData;
