import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { FileCaseFormData } from '@/components/BackStage/5_FileCase/FileCaseCreateModal.vue';

/**
 * @todo 新增 FileCase 回應
 */
export interface FileCaseManagementCreateResp extends GeneralResp {}

/**
 * @todo 新增 FileCase
 */
const FileCaseManagementCreate = (fileCase: FileCaseFormData): Promise<GeneralResp> => {
    const url = RouteUrlList.API_FileCaseManagementCreate.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };

    // 轉換資料格式以符合後端 API 期望
    const requestData = {
        business: fileCase.business,
        uploadSystemID: fileCase.uploadSystemID,
        uploadEmployeeNo: fileCase.uploadEmployeeNo,
        permission: fileCase.permission !== null ? parseInt(fileCase.permission) : 0 // 確保 permission 為數字
    };

    return ApiClient.post(url, requestData, config).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default FileCaseManagementCreate;
