import { ServiceAlertApiClient } from '@/libs/ServiceAlertApiClient';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as Common from '@/libs/Common';
import { useClientStore } from '@/stores/modules/clientInfo';

/**
 * @todo 登入檢查(for 9分鐘逾時提醒第10分鐘後登出)
 * @param refreshResult
 * @param handleError
 * @returns
 */
const ServiceAlertLoginCheck = (refreshResult: boolean = false, handleError: boolean = true): Promise<boolean> => {
    Common.ConsoleLog('[ServiceAlert.LoginCheck] Api Start');
    const store = useClientStore();
    Common.ConsoleLog('[ServiceAlert.LoginCheck] Api : routeInitState = ' + store.routeInitState);

    if (store.routeInitState == false || refreshResult === true) {
        const url = RouteUrlList.API_CheckIfLogin.path;
        const config = {
            params: {
                showOverlay: ApiClient.paramTypeEnum.false,
                getServiceAlert: ApiClient.paramTypeEnum.false
            }
        };
        return ServiceAlertApiClient.post(url, {}, config)
            .then((res: AxiosResponse<GeneralResp>) => {
                Common.ConsoleLog('[ServiceAlert.LoginCheck] Api isLogin call Api res :' + res.data.status);
                store.setLoginState(res.data.status);
                return res.data.status;
            })
            .catch((e) => {
                if (handleError) {
                    ApiClient.HandleError(e);
                }
                return false;
            });
    } else {
        return new Promise<boolean>((resolve) => {
            Common.ConsoleLog('[ServiceAlert.LoginCheck] Api isLogin from store :' + store.isLogin.valueOf());
            resolve(store.isLogin.valueOf());
        });
    }
};
export default ServiceAlertLoginCheck;
