import type { GeneralResp } from '@/models/GeneralResp';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { AxiosResponse } from 'axios';

export interface FileCasePermissionWhiteListItem {
    caseNo: string;
    business: string;
}

export interface FileCasePermissionWhiteListViewDataResp extends GeneralResp {
    result: {
        items: FileCasePermissionWhiteListItem[];
        totalCount: number;
    };
}

/**
 * 取得文件類別權限白名單資料
 */
const FileCasePermissionWhiteListViewData = (caseNo: string): Promise<FileCasePermissionWhiteListViewDataResp> => {
    const url = RouteUrlList.API_FileCasePermissionWhiteListViewData.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    const requestData = {
        caseNo: caseNo
    };
    return ApiClient.post(url, requestData, config).then(
        (res: AxiosResponse<FileCasePermissionWhiteListViewDataResp>) => {
            return res.data;
        }
    );
};

export default FileCasePermissionWhiteListViewData;
