import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';

/**
 * 軟刪除檔案資訊
 */
const FileInfosManagementDelete = (fileNo: string): Promise<GeneralResp> => {
    const url = RouteUrlList.API_FileInfosManagementDelete.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };

    return ApiClient.post(url, { fileNo }, config).then((res: AxiosResponse<GeneralResp>) => {
        return res.data;
    });
};

export default FileInfosManagementDelete;