import { createRouter, createWebHistory } from 'vue-router';
import { GetRouteDataByUrl, RouteUrlList, type IRouteUrlListElement } from '@/libs/RouteList';
import { useClientStore } from '@/stores/modules/clientInfo';
import * as ApiInstance from '@/libs/ApiClient';
import API from '@/api';
import isMobile from 'ismobilejs';
import * as Common from '@/libs/Common';
const routes = [
    {
        //  登入頁
        path: RouteUrlList.Login.path,
        name: RouteUrlList.Login.name,
        component: () => import('@/components/BackStage/1_LoginPage/LoginPage.vue'),
        meta: { layout: 'login' }
    },

    {
        //  backstage首頁
        path: RouteUrlList.Index.path,
        name: RouteUrlList.Index.name,
        component: () => import('@/components/BackStage/2_Sys/SystemListManagement.vue'),
        meta: { layout: 'admin' }
    },

    {
        //  backstage首頁
        path: RouteUrlList.SysManagement.path,
        name: RouteUrlList.SysManagement.name,
        component: () => import('@/components/BackStage/2_Sys/SystemListManagement.vue'),
        meta: { layout: 'admin' }
    },

    {
        //  backstage Busniess
        path: RouteUrlList.BusinessManagement.path,
        name: RouteUrlList.BusinessManagement.name,
        component: () => import('@/components/BackStage/3_Business/BusinessManagement.vue'),
        meta: { layout: 'admin' }
    },
    {
        //  backstage 4_DocType
        path: RouteUrlList.DocTypeManagement.path,
        name: RouteUrlList.DocTypeManagement.name,
        component: () => import('@/components/BackStage/4_DocType/DocTypeManagement.vue'),
        meta: { layout: 'admin' }
    },
    {
        //  backstage 5_FileCase
        path: RouteUrlList.FileCaseManagement.path,
        name: RouteUrlList.FileCaseManagement.name,
        component: () => import('@/components/BackStage/5_FileCase/FileCaseManagement.vue'),
        meta: { layout: 'admin' }
    },
    {
        //  backstage 6_FileInfos
        path: RouteUrlList.FileInfosManagement.path,
        name: RouteUrlList.FileInfosManagement.name,
        component: () => import('@/components/BackStage/6_FileInfos/FileInfosManagement.vue'),
        meta: { layout: 'admin' }
    },
    {
        //  backstage 7_FileLabelDTO
        path: RouteUrlList.FileLabelManagement.path,
        name: RouteUrlList.FileLabelManagement.name,
        component: () => import('@/components/BackStage/7_FileLabel/FileLabelManagement.vue'),
        meta: { layout: 'admin' }
    },
    {
        path: RouteUrlList.StateError.path,
        name: RouteUrlList.StateError.name,
        component: () => import('@/components/StateError.vue'),
        meta: { layout: 'admin' }
    },
    {
        path: '/:pathMatch(.*)*',
        name: '404',
        component: () => import('@/components/StateError.vue'),
        meta: { layout: 'admin' }
    }
];

const router = createRouter({
    history: createWebHistory(import.meta.env.BASE_URL),
    routes
});
import { useLeaveProtectionStore } from '@/stores/modules/leaveProtection';

// 在這裡設置全局守衛
router.beforeEach(async (to, from, next) => {
    console.log('[Debug]', to.path);
    const currentPath = to.fullPath;
    const _targetPath = to.path.trim().toLowerCase();
    const routeData: IRouteUrlListElement = GetRouteDataByUrl(_targetPath);
    // 防止提開或刷新
    const leaveProtectionStore = useLeaveProtectionStore();
    if (routeData.leaveProtection === true) {
        leaveProtectionStore.setUnsavedChanges(true);
    } else {
        leaveProtectionStore.setUnsavedChanges(false);
    }

    // 錯誤頁skip下面登入判斷
    if (
        routeData.name === RouteUrlList.StateError.name ||
        _targetPath === RouteUrlList.StateError.path.trim().toLowerCase() ||
        routeData.name === RouteUrlList.Login.name ||
        _targetPath === RouteUrlList.Login.path.trim().toLowerCase()
    ) {
        next(); // 繼續導航
        return;
    }

    // window.scrollTo({ top: 0, behavior: 'smooth' });
    const store = useClientStore();
    let isLogin = store.isLogin.valueOf();
    if (!isLogin) {
        isLogin = await API.ServiceAlertLoginCheck(true); // 登入檢查
    }

    store.setRouteInit(true);
    store.setLoginState(isLogin.valueOf());
    store.setIsMobileState(isMobile(window.navigator).any);
    store.setIsLoaded(false);

    Common.ConsoleLog('[router.index] 路由 : ' + routeData?.name);
    ApiInstance.GetServiceAlertInstance().ClearIdleCheckTimeoutId();
    if (isLogin === false) {
        // redirect the user to the login page
        if (routeData.requiredLogin === true) {
            return next({
                name: RouteUrlList.Login.name,
                query: {
                    ...to.query
                    //timestamp: Date.now().toString() // 添加時間戳
                }
            });
        } else {
            next(); // 繼續導航
            return;
        }
    } else {
        next(); // 繼續導航
        return;
    }
});

router.afterEach((to, from) => {
    const store = useClientStore();
    store.setIsLoaded(true);
    // 頁面SEO優化 安全地設定 <link rel="canonical">
    SetCanonicalLink();
});

/**
 * @todo 頁面SEO優化
 * @param canonicalUrl
 * @returns
 */
function SetCanonicalLink(): boolean {
    try {
        const TRUSTED_HOSTS: RegExp[] = [/\.esunsec\.com\.tw$/, /\.testesunsec\.com\.tw$/];

        const origin: string = window.location.origin;
        const pathname: string = window.location.pathname;

        // 驗證 origin 來源
        const isValidOrigin: boolean = TRUSTED_HOSTS.some((hostRegex) => hostRegex.test(new URL(origin).hostname));
        const isValidPathname: boolean = /^\/[a-zA-Z0-9._~:/?#[\]@!$&'()*+,;=-]*$/.test(pathname);

        if (!isValidOrigin || !isValidPathname) {
            Common.ConsoleLog(`[router.index] Invalid Canonical URL detected will not set canonical link`);
            return false;
        }

        // 編碼canonicalUrl
        const canonicalUrl: string = encodeURI(origin + pathname).replace(/[\\"']/g, '');

        // 安全設置 canonical 標籤
        (function setCanonicalUrl(url: string): void {
            let link: HTMLLinkElement | null = document.querySelector('link[rel="canonical"]');
            if (!link) {
                link = document.createElement('link');
                link.rel = 'canonical';
                document.head.appendChild(link);
            }
            link.href = url;
        })(canonicalUrl);

        return true;
    } catch (e) {
        Common.ConsoleLog(e);
        return false;
    }
}

export default router;
