import type { GeneralResp } from '@/models/GeneralResp';
import type { AxiosResponse } from 'axios';
import * as ApiClient from '@/libs/ApiClient';
import { RouteUrlList } from '@/libs/RouteList';
import type { SystemDTO } from '@/models/DTO/SystemDTO';

/**
 * @todo 初始化設定值
 */
export interface SystemListManagementViewDataResp extends GeneralResp {
    result: {
        items: SystemDTO[];
        totalCount: number;
        pageNumber: number;
        pageSize: number;
    };
}

/**
 * @todo 登入檢查
 */
const SystemListManagementViewData = (
    systemName: string | null,
    pageNumber: number,
    pageSize: number
): Promise<SystemListManagementViewDataResp> => {
    const url = RouteUrlList.API_SysManagementViewData.path;
    const config = {
        params: { showOverlay: ApiClient.paramTypeEnum.true, getServiceAlert: ApiClient.paramTypeEnum.true }
    };
    return ApiClient.post(url, { systemName: systemName, pageNumber: pageNumber, pageSize: pageSize }, config).then(
        (res: AxiosResponse<SystemListManagementViewDataResp>) => {
            return res.data;
        }
    );
};

export default SystemListManagementViewData;
